#Project {
  display: flex;
  flex-direction: column;
  padding-top: 6vh;
}

#Project article {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0 12.5vh 12.5vh 12.5vh;
}

#Project h1 {
  font-size: 10em;
  font-weight: 900;
  margin-left: 10vw;
  color: rgb(230, 218, 218);
  text-shadow: -5px 5px rgb(45, 45, 45);
}

.major {
  width: 30rem;
  height: 25rem;
  display: flex;
  margin: 1rem;
  position: relative;
  flex-direction: column;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.75), 10px 10px rgba(0, 0, 0, 0.5), 15px 15px rgba(0, 0, 0, 0.25);
  transition: box-shadow 0.25s ease-in-out;
}

.major img {
  height: 20rem;
  width: 30rem;
  object-fit: cover;
  justify-content: center;
}

.major header {
  font-size: 200%;
  font-weight: 900;
  padding: 1rem;
  text-align: center;
  text-shadow: 1px 1px black;
}

.major p {
  padding: 1rem 0.5rem 0 0.5rem;
  text-align: left;
  font-size: 125%;
  font-weight: 700;
}

.major section {
  align-self: flex-end;
  height: 5rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: rgb(230, 218, 218, 0.505);
}

.major section a {
  position: relative;
  justify-content: space-between;
  padding: 0 10%;
}

.major text {
  color: black;
}


.fade-image,
.fade-text {
  position: absolute;
  top: 0;
}

.fade-text {
  opacity: 0;
}

.fade-box:hover>.fade-text {
  opacity: 1;
  transition: 0.5s ease-in;
}

.fade-box:hover>img {
  opacity: 0.1;
  transition: 1s ease-out;
}

.major:hover {
  box-shadow: 5px 5px rgba(0, 0, 0, 0.75), 10px 10px rgba(0, 0, 0, 0.5), -5px -5px rgba(0, 0, 0);
}

